.TagsList {
	margin-bottom: 20px;
}

.NavTagsList {
	position: relative;
	padding: 5px;
}

.NavTagsList button {
	background-color: transparent;
	border: none;
	outline: none;
}

.NavTagsListTop {
	display: none;
	height: 20px;
	width: 100%;
	position: absolute;
	top: 0;
	left: 0;
	right: 0;
	z-index: 50;
	background: -moz-linear-gradient(top, rgba(255, 255, 255, 1) 0%, rgba(255, 255, 255, 0.99) 1%, rgba(255, 255, 255, 0.71) 55%, rgba(255, 255, 255, 0) 100%);
	background: -webkit-linear-gradient(top, rgba(255, 255, 255, 1) 0%, rgba(255, 255, 255, 0.99) 1%, rgba(255, 255, 255, 0.71) 55%, rgba(255, 255, 255, 0) 100%);
	background: linear-gradient(to bottom, rgba(255, 255, 255, 1) 0%, rgba(255, 255, 255, 0.99) 1%, rgba(255, 255, 255, 0.71) 55%, rgba(255, 255, 255, 0) 100%);
	filter: progid:DXImageTransform.Microsoft.gradient(startColorstr='#ffffff', endColorstr='#00ffffff', GradientType=0);
}

.NavTagsListBottom {
	display: none;
	height: 20px;
	width: 100%;
	position: absolute;
	bottom: 0;
	left: 0;
	right: 0;
	z-index: 50;
	background: -moz-linear-gradient(top, rgba(255, 255, 255, 0) 0%, rgba(255, 255, 255, 0.71) 45%, rgba(255, 255, 255, 0.99) 99%, rgba(255, 255, 255, 1) 100%);
	background: -webkit-linear-gradient(top, rgba(255, 255, 255, 0) 0%, rgba(255, 255, 255, 0.71) 45%, rgba(255, 255, 255, 0.99) 99%, rgba(255, 255, 255, 1) 100%);
	background: linear-gradient(to bottom, rgba(255, 255, 255, 0) 0%, rgba(255, 255, 255, 0.71) 45%, rgba(255, 255, 255, 0.99) 99%, rgba(255, 255, 255, 1) 100%);
	filter: progid:DXImageTransform.Microsoft.gradient(startColorstr='#00ffffff', endColorstr='#ffffff', GradientType=0);
}

.NavTagsList:hover .NavTagsListTop {
	display: block;
}

.NavTagsList:hover .NavTagsListBottom {
	display: block;
}

.ListEmpty {
	font-size: .9em;
	color: #999999;
}

.TagsListTitle {
	margin: 0 0 15px;
}

.SearchInput {
	margin: 0 0 10px;
}